.compare_gamescreen{
    /* border: 2px solid red; */
    width : 60vw;
    height: 80vh;
    margin: 10vh auto;
}
.compareQuestion{
    /* border: 1px solid red; */
    width: 60%;
    padding: 3%;
    margin: 0 auto;
    background-color: rgba(212, 179, 212, 0.4);
    border-radius: 15px;
    font-weight: 900;
}
.seasaw{
    /* border: 1px solid red; */
    width: 100%;
    height: 60%;
    display: flex;
    margin-top: 5%;
}
.seasaw .section1{
    /* border: 1px solid red; */
    width: 50%;
    height: 100%;
    position: relative;
}
.seasaw .section2{
    /* border: 1px solid red; */
    width: 50%;
    height: 100%;
    position: relative;
}
.section1 img ,.section2 img{
    width: 30%;
    position: absolute;
    left: 35%;
}
.buttons{
    /* border: 1px solid red; */
    width: 100%;
    height: 10%;
    display: flex;
}
.buttons .holder1{
    /* border: 1px solid red; */
    width: 50%;
    height: 100%;
    position: relative;
}
.buttons .holder2{
    /* border: 1px solid red; */
    width: 50%;
    height: 100%;
    position: relative;
}
.holder1 button{
    background-color: rgba(206, 98, 79, 0.788);
    border: none;
    width: 10%;
    margin: 3px;
    color: white;
    border-radius: 15px;
    height: 50%;
    /* height: 20%; */
}
.holder2 button{
    background-color: rgba(206, 98, 79, 0.788);
    border: none;
    width: 10%;
    margin: 3px;
    color: white;
    border-radius: 15px;
    height: 50%;
    /* height: 20%; */
}

